module Yosys

open Fable.Core
open Fable.Core.JsInterop
open Fable.Import
open Electron
open Node.Exports
open Fable.PowerPack

type Init() = 
    [<Emit("YosysJS.load_viz();")>]
    member __.loadViz with get() = jsNative



type Synthesizer() = 
    [<Emit("ys.write_file($1, document.getElementById($2).value)")>]
    member __.writeFile(filename: string, element: string) = jsNative

    [<Emit("ys.run('design -reset; read_verilog input.v; synth -run coarse; show -stretch')")>]
    member __.run with get() = jsNative

    [<Emit("YosysJS.dot_into_svg(ys.read_file('show.dot'), 'svg');")>]
    member __.getSvg with get() = jsNative

    [<Emit("window.setTimeout(work, 100);")>]
    member __.setTimeout with get() = jsNative

    [<Emit("YosysJS.create('ys', ysReady);")>]
    member __.ys with get() = jsNative


